import axios from '@nextcloud/axios';
import { generateUrl } from '@nextcloud/router';
export default {
  data() {
    return {
      form: {
        name: '',
        type: 'string'
      }
    };
  },
  methods: {
    async submitForm() {
      if (window.confirm(this.form.name + ' wirklich erstellen?')) {
        console.log(this.form);
        // Hier knnen Sie dann Ihre Logik zum Speichern der Daten einfgen
        const response = await axios.post(generateUrl('/apps/itamapp/customfields'), {
          name: this.form.name,
          type: this.form.type
        });
        console.log(response);
      }
    }
  }
};