  methods: {
    getRaumName(locationId) {
      if (Array.isArray(this.raeume)) {
        const raum = this.raeume.find(r => r.id === Number(locationId));
        return raum ? raum.raumName : '';
      }
      return '';
    },
    getPersonName(personId) {
      if (Array.isArray(this.personen)) {
        const person = this.personen.find(p => p.id === Number(personId));
        return person ? person.name : '';
      }
      return '';
    },
    exportCSV() {
      let csv = this.$store.state.assets.defaultAssetFields.join() + ',';
      csv += this.fields.map(field => field.name).join(',') + '\n';
      this.inventar.forEach(asset => {
        csv += "".concat(asset.inventarnummer, ",").concat(asset.rechnungsdatum, ",").concat(asset.seriennummer, ",").concat(this.getRaumName(asset.locationId), ",").concat(this.getPersonName(asset.personId), ",");
        csv += this.fields.map(field => asset[field.name]).join(',').replace(/,/g, ',');
        csv += '\n';
      });
      let blob = new Blob([csv], {
        type: 'text/csv;charset=utf-8;'
      });
      let link = document.createElement('a');
      let url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', 'export.csv');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    createCSV() {},
    changePage(page) {
      this.currentPage = page;
    },
    logData(data) {
      console.log(data);
    },
    push(id, asset) {
      this.$router.push({
        name: 'AssetDetail',
        params: {
          id: id
        },
        props: {
          asset: asset
        }
      });
    },
    async test() {
      const builder = getFilePickerBuilder('Whlen Sie eine PDF-Datei aus').setMultiSelect(false).setModal(true).setType(FilePickerType.Choose).allowDirectories(false);
      const picker = builder.build();
      this.selectedPdfPath = await picker.pick();
      console.log('Selected PDF: ' + this.selectedPdfPath);

      //const response = await axios.get(generateUrl('/apps/itamapp/personraum'));
      //console.log('PersonRaum: ', response.data);
    },

    editSelected() {
      console.log('Selected items:', this.selected);
    }
  },
  computed: {
    pages() {
      return Math.ceil(this.filteredInventar.length / this.itemsPerPage);
    },
    paginatedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredInventar.slice(start, end);
    },
    filteredInventar() {
      if (this.search === '') {
        return this.inventar;
      } else {
        return this.inventar.filter(item => {
          let raum = this.getRaumName(item.locationId);
          let person = this.getPersonName(item.personId);
          //Prfen der default Felder
          const matchesInNormalFields = item.inventarnummer && item.inventarnummer.includes(this.search) || item.rechnungsdatum && item.rechnungsdatum.includes(this.search) || item.seriennummer && item.seriennummer.includes(this.search) || item.beschreibung && item.beschreibung.includes(this.search) || item.locationId && raum.includes(this.search) || item.personId && person.includes(this.search);
          if (matchesInNormalFields) {
            return true;
          }

          //Prfen der CustomFields
          const matchesInCustomFields = this.fields.some(field => {
            const fieldName = field.name;
            return item[fieldName] && item[fieldName].includes(this.search);
          });
          return matchesInCustomFields;
        });
      }
    },
    inventar() {
      var groupedCustomFields = {};
      var assets = this.$store.state.assets.inventar;
      var customFields = this.$store.state.assets.customFieldValues;
      //Ordne die CustomFieldValues den zugehrigen Assets zu
      customFields.forEach(field => {
        if (!groupedCustomFields[field.assetId]) {
          groupedCustomFields[field.assetId] = [];
        }
        groupedCustomFields[field.assetId].push(field);
      });
      //Fge die CustomFieldValues den Asset-Objekten hinzu
      assets.forEach(asset => {
        var cfAsset = groupedCustomFields[asset.id];
        if (cfAsset === undefined) {
          return;
        }
        cfAsset.forEach(cf => {
          asset[cf.name] = cf.value;
        });
        console.log('Vorher: ' + JSON.stringify(asset));
        asset.customFields = groupedCustomFields[asset.id];
        console.log(JSON.stringify(asset));
      });
      return assets;
    },
    raeume() {
      return this.$store.getters.getRaum;
    },
    personen() {
      return this.$store.getters.getPersonen;
    },
    fields() {
      return this.$store.state.assets.customFields;
    }
  },
  async created() {
    await fetchAssets();
    console.log('Created ' + this.inventoryList);
  },
