import axios from '@nextcloud/axios';
import { generateUrl } from '@nextcloud/router';
export default {
  data() {
    return {
      persons: [],
      form: {
        name: ''
      }
    };
  },
  methods: {
    async submitForm() {
      if (window.confirm(this.form.name + ' wirklich erstellen?')) {
        console.log(this.form);
        // Hier knnen Sie dann Ihre Logik zum Speichern der Daten einfgen
        const response = await axios.post(generateUrl('/apps/itamapp/person'), {
          name: this.form.name
        });
        console.log(response);
        this.getPersons(); // Aktualisieren Sie die Personenliste nach erfolgreichem Speichern
      }
    },

    async getPersons() {
      console.log('getPersons');
      const response = await axios.get(generateUrl('/apps/itamapp/person'));
      console.log(response);
      this.persons = response.data;
    },
    async deletePerson(id) {
      if (window.confirm('Sind Sie sicher, dass Sie diese Person lschen mchten?')) {
        const response = await axios.delete(generateUrl("/apps/itamapp/person/".concat(id)));
        if (response.status === 200) {
          this.getPersons(); // Aktualisieren Sie die Personenliste nach erfolgreichem Lschen
        }
      }
    }
  },

  mounted() {
    console.log('mounted');
    this.getPersons(); // Personenliste beim Laden der Komponente abrufen
  }
};