import axios from '@nextcloud/axios';
import { generateUrl } from '@nextcloud/router';
export default {
  data() {
    return {
      raeume: [],
      form: {
        name: ''
      }
    };
  },
  methods: {
    async submitForm() {
      if (window.confirm(this.form.name + ' wirklich erstellen?')) {
        console.log(this.form);
        // Hier knnen Sie dann Ihre Logik zum Speichern der Daten einfgen
        const response = await axios.post(generateUrl('/apps/itamapp/raum'), {
          name: this.form.name
        });
        console.log(response);
        this.getRaueme(); // Aktualisieren Sie die Personenliste nach erfolgreichem Speichern
      }
    },

    async getRaueme() {
      console.log('getRaeume');
      const response = await axios.get(generateUrl('/apps/itamapp/raum'));
      console.log(response);
      this.raeume = response.data;
    },
    async deleteRaum(id) {
      if (window.confirm('Sind Sie sicher, dass Sie diese Person lschen mchten?')) {
        const response = await axios.delete(generateUrl("/apps/itamapp/raum/".concat(id)));
        if (response.status === 200) {
          this.getRaueme(); // Aktualisieren Sie die Personenliste nach erfolgreichem Lschen
        }
      }
    }
  },

  mounted() {
    console.log('mounted');
    this.getRaueme(); // Personenliste beim Laden der Komponente abrufen
  }
};